<template>
    <div class="wrap">
        <el-row>
            <el-button type="primary" @click="addShow">新增</el-button>
        </el-row>
        <div class="tableCon">
            <el-row>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark typeMain">
                        <p style="padding-bottom: 20px">质量分析标准</p>
                            <el-table
                            :data="tableData"
                            style="width: 100%;border: 1px solid #eee;border-bottom: none;">
                            <el-table-column
                                prop="num"
                                label="编号">
                            </el-table-column>
                            <el-table-column
                                prop="msg1"
                                label="标准">
                            </el-table-column>
                            <el-table-column
                                prop="msg"
                                label="说明">
                            </el-table-column>
                            <el-table-column label="操作">
                                <template slot-scope="scope">
                                    <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                </el-col>
            </el-row>
        </div>

        <div class="alert_bg" v-show="save.status"></div>
        <div class="alert" v-show="save.status">
            <div class="title" style="padding-bottom: 30px;">新增</div>
            <div class="item">
                <p>编号：</p>
                <el-input v-model="save.num" placeholder="请输入内容"></el-input>
            </div>
            <div class="item">
                <p>标准：</p>
                <el-input v-model="save.msg1" placeholder="请输入内容"></el-input>
            </div>
            <div class="item">
                <p>说明：</p>
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="请输入内容"
                    v-model="save.msg">
                </el-input>
            </div>
            <div class="item">
                <p>分类：</p>
                <div>
                    <template>
                        <el-radio v-model="save.identifiernum" label="1">分类1</el-radio>
                        <el-radio v-model="save.identifiernum" label="2">分类2</el-radio>
                    </template>
                </div>
            </div>
            <el-row class="sub_b">
                <el-button size="mini" @click="close">取消</el-button>
                <el-button type="primary" size="mini" @click="sub">确认</el-button>
            </el-row>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            save: {
                id: '',
                status: false, // add edit false
                identifiernum: "1", // 分类
                msg: '', // 说明
                msg1: '', //  标准
                num: '', // 编号
            },
            currentPage: 1, //初始页
            pagesize:10,    //    每页的数据
            count: 0,
            tableData: [],
            page: 0
        }
    },
    methods: {
        // 新增显示
        addShow() {
            this.save = {
                status: 'add', // add edit false
                identifiernum: "1", // 分类
                msg: '', // 说明
                msg1: '', //  标准
                num: '', // 编号
            }
        },
        // 取消
        close() {
            this.save.status = false;
        },
        // 确认
        sub() {
            switch(this.save.status) {
                case "add":
                    this.newQualitystandard();
                    break;
                case "edit":
                    this.changeQualitystandard();
                    break;
            }
            this.save.status = false;
            this.geTqualitystandard();
            console.log(this.save);
        },
        // 获取列表
        geTqualitystandard() {
            console.log('获取列表')
            api.geTqualitystandard().then(res => {
                console.log(res.data.result)
                this.tableData = res.data.result;
            })
        },
        // 确定新增
        newQualitystandard() {
            api.newQualitystandard({
                identifiernum: this.save.identifiernum,// 分类
                msg: this.save.msg,// 说明
                msg1: this.save.msg1,//  标准
                num: this.save.num// 编号
            }).then(res => {
                console.log(res)
                if(res.data.status == "ok") {
                    this.geTqualitystandard();
                    this.$message({
                        type: 'success',
                        message: '新增成功!'
                    });
                } else {
                    this.$message({
                        message: '新增失败！',
                        type: 'warning'
                    });
                }
            })
        },
        // 修改确定
        changeQualitystandard() {
            api.changeQualitystandard(this.save.id, {
                identifiernum: this.save.identifiernum, // 分类
                msg: this.save.msg, // 说明
                msg1: this.save.msg1, //  标准
                num: this.save.num // 编号
            }).then(res => {
                console.log(res);
                if(res.data.status == "ok") {
                    this.$message({
                        type: 'success',
                        message: '修改成功!'
                    });
                    this.geTqualitystandard();
                } else {
                    this.$message({
                        message: '修改失败！',
                        type: 'warning'
                    });
                }
            })
        },
        // 修改显示
        handleEdit(index, val) {
            console.log(val)
            this.save = {
                id: val.id,
                status : "edit",
                identifiernum: val.identifiernum, // 分类
                msg: val.msg, // 说明
                msg1: val.msg1, //  标准
                num: val.num // 编号
            }
        },
        // 删除
        handleDelete(index, val) {
            console.log(val.id);
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                api.delQualitystandard(val.id).then(res => {
                    console.log(res)
                    if(res.data.status == "ok") {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                        this.geTqualitystandard();
                    } else {
                        this.$message({
                            message: '删除失败！',
                            type: 'warning'
                        });
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        }
    },
    created() {
        this.geTqualitystandard();
    },
}
</script>
<style lang="stylus" scoped>
.wrap 
    border-radius 10px
    overflow hidden
    padding 20px
    .tableCon
        background #ffffff
        margin-top 20px
        padding 20px
        border-radius 12px
        overflow hidden
    .alert_bg
        width 100%
        height 100vh
        background rgba(0,0,0,.5)
        position fixed
        left 0
        top 0
        z-index 900
    .alert
        width 400px
        background #fff
        position fixed
        left 50%
        top 50%
        transform translate(-50%, -50%)
        z-index 999
        padding 20px
        border-radius 4px
        .item 
            display flex
            align-items center
            white-space nowrap
            padding 10px 0
        .sub_b
            display flex
            justify-content flex-end
            padding-top 30px
</style>



